* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }
  
  .app {
    display: flex;
    flex-direction: column; /* Corrected spelling */
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
  }
  
  .Parent-Container {
    background-color: burlywood;
    color: antiquewhite;
  }
  
  .container {
    box-shadow: 0px 0px 15px 10px rgb(106, 91, 100);
    background-color: rgb(252, 233, 233);
    width: 30%;
    height: 80vh;
    padding: 20px; /* Added padding for better spacing */
  }
  
  .title {
    margin: 34px 0;
    text-align: center;
  }
  
  .labels {
    font-size: 22px;
    padding-left: 30px;
  }
  
  .bmi-input {
    width: 80%;
    height: 30px;
    margin-left: 33px;
    margin-top: 12px;
  }
  
  .btn {
    margin-top: 20px;
    background-color: #4caf50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .btn-reload {
    background-color: #008cba;
  }
  
  .btn-geolocation {
    background-color: #ff9800;
  }
  
  .btn-open-location {
    background-color: #e53935;
  }
  
  .result {
    margin-top: 20px;
    text-align: center;
  }
  